import menuItems from './menu.json';
import menuItemTemplate from './templates/menu-item.hbs';

import './styles.css';
import { brotliDecompressSync } from 'zlib';

const refs = {
  menuPost: document.querySelector('.menu'),
  changeBtn: document.querySelector('.js-switch-input'),
  body: document.querySelector('body'),
};

const Theme = {
  LIGHT: 'light-theme',
  DARK: 'dark-theme',
};


createMenuLayOut(menuItems);

function createMenuLayOut(menuItems) {
  const menuMarkup = menuItems.map(menuItem => menuItemTemplate(menuItem)).join('');
  refs.menuPost.insertAdjacentHTML('afterbegin', menuMarkup);
}

refs.body.classList.add(Theme.LIGHT);

refs.changeBtn.addEventListener('change', (event) => {
  if (event.target.checked) {
    refs.body.classList.toggle(Theme.DARK);
    localStorage.setItem('Theme', 'Theme.DARK');
  }
  else {
    refs.body.classList.remove(Theme.DARK);
    localStorage.removeItem('Theme');
  };
})

const themeSettings = localStorage.getItem('Theme');


if(themeSettings === 'Theme.DARK') {
  refs.body.classList.add(Theme.DARK);
  refs.changeBtn.checked = true;
}
